import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { listProducts } from "../actions/productActions";
import LoadingBox from "../components/LoadingBox";
import MessageBox from "../components/MessageBox";
import Product from "../components/Product";
import Rating from "../components/Rating";
import { prices, ratings } from "../constants/utils.js";

export default function SearchScreen(props) {
	const {
		name = "all",
		category = "all",
		min = 0,
		max = 0,
		rating = 0,
		order = "newest",
		pageNumber = 1,
	} = useParams();
	const dispatch = useDispatch();
	const productList = useSelector((state) => state.productList);
	const { loading, error, products, page, pages } = productList;

	const productCategoryList = useSelector(
		(state) => state.productCategoryList
	);
	const {
		loading: loadingCategories,
		error: errorCategories,
		categories,
	} = productCategoryList;

	useEffect(() => {
		dispatch(
			listProducts({
				name: name !== "all" ? name : "",
				category: category !== "all" ? category : "",
				min,
				max,
				rating,
				order,
				pageNumber,
			})
		);
	}, [category, dispatch, max, min, name, order, pageNumber, rating]);

	const getFilterUrl = (filter) => {
		const filterPage = filter.page || pageNumber;
		const filterCategory = filter.category || category;
		const filterName = filter.name || name;
		const filterRating = filter.rating || rating;
		const sortOrder = filter.order || order;
		const filterMin = filter.min ? filter.min : filter.min === 0 ? 0 : min;
		const filterMax = filter.max ? filter.max : filter.max === 0 ? 0 : max;
		return `/search/category/${filterCategory}/name/${filterName}/min/${filterMin}/max/${filterMax}/rating/${filterRating}/order/${sortOrder}/pageNumber/${filterPage}`;
	};

	return (
		<div>
			<div className="row">
				{loading ? (
					<LoadingBox></LoadingBox>
				) : error ? (
					<MessageBox variant="failed-action">{error}</MessageBox>
				) : (
					<>
						{/* 			<div>
							{products.length} rezultat
							{products.length !== 1 && <span>a</span>}
						</div> */}
					</>
				)}
				<div>
					Sortiraj po
					<select
						value={order}
						onChange={(e) => {
							props.history.push(
								getFilterUrl({ order: e.target.value })
							);
						}}
					>
						<option value="newest">Najnovije</option>
						<option value="lowest">Cijena: Manja prije</option>
						<option value="highest">Cijena: Viša prije</option>
						<option value="toprated">Ocjene</option>
					</select>
				</div>
			</div>
			<div className="row top">
				<div className="col-1">
					<h3>
						Filteri <i className="fas fa-filter"></i>
					</h3>
					<h3>Kategorije</h3>
					<div>
						{loadingCategories ? (
							<LoadingBox></LoadingBox>
						) : errorCategories ? (
							<MessageBox variant="failed-action">
								{errorCategories}
							</MessageBox>
						) : (
							<ul>
								<li>
									<Link
										className={
											"all" === category ? "active" : ""
										}
										to={getFilterUrl({ category: "all" })}
									>
										Sve
									</Link>
								</li>
								{categories.map((c) => (
									<li key={c}>
										<Link
											className={
												c === category ? "active" : ""
											}
											to={getFilterUrl({ category: c })}
										>
											{c}
										</Link>
									</li>
								))}
							</ul>
						)}
					</div>
					<div>
						<h3>Cijena</h3>
						<ul>
							{prices.map((p) => (
								<li key={p.name}>
									<Link
										to={getFilterUrl({
											min: p.min,
											max: p.max,
										})}
										className={
											`${p.min}-${p.max}` ===
											`${min}-${max}`
												? "active"
												: ""
										}
									>
										{p.name}
									</Link>
								</li>
							))}
						</ul>
					</div>
					<div>
						<h3>Ocjene</h3>
						<ul>
							{ratings.map((r) => (
								<li key={r.name}>
									<Link
										to={getFilterUrl({ rating: r.rating })}
										className={
											`${r.rating}` === `${rating}`
												? "active"
												: ""
										}
									>
										<Rating
											caption={" & više"}
											rating={r.rating}
										></Rating>
									</Link>
								</li>
							))}
						</ul>
					</div>
				</div>
				<div className="col-3">
					{loading ? (
						<LoadingBox></LoadingBox>
					) : error ? (
						<MessageBox variant="failed-action">{error}</MessageBox>
					) : (
						<>
							{products.length === 0 && (
								<MessageBox>Nema proizvoda</MessageBox>
							)}
							<div className="row center">
								{products.map((product) => (
									<Product
										key={product._id}
										product={product}
									></Product>
								))}
							</div>
							<div className="row center pagination">
								{[...Array(pages).keys()].map((x) => (
									<Link
										className={
											x + 1 === page ? "active" : ""
										}
										key={x + 1}
										to={getFilterUrl({ page: x + 1 })}
									>
										{x + 1}
									</Link>
								))}
							</div>
						</>
					)}
				</div>
			</div>
		</div>
	);
}
